import React, { Component } from 'react';

class SlideshowPage extends Component {
    render() {
        return (
            <article>
                <span>{this.props.id}</span>
                <h1>{this.props.title}</h1>
                <p>{this.props.description}</p>
            </article>
        );
    }
}

export default SlideshowPage;


