import React, { Component } from 'react'
import { Link } from 'react-router-dom'

class Navigation extends Component {
  render() {
    return (
      <nav role="custom-dropdown">
        <div className="group-buttons">
          <Link className="button button-action" to='/convite'><span>Pedir meu convite</span></Link>
        </div>
        <input type="checkbox" id="button" />
        <label htmlFor="button" onclick />
        <ul>
          <li><Link className="wide" hash="#intro" to='/'>Home</Link></li>
          <li><a href="#slider" className="wide">Como Funciona</a></li>
          <li><a className="wide">Para Academias</a></li>
          {/* <li><Link className="button-nav wide" to='/login'><span>Entrar</span></Link></li> */}
        </ul>
      </nav>
    );
  }
}

export default Navigation;


