import React, { Component } from 'react';
import Slider from 'react-slick';
import SliderItem from './slideshowPage'

import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

function SampleNextArrow(props) {
    const { id, className, onClick } = props
    return (
        <i id="next" className={"material-icons"} onClick={onClick}>arrow_forward</i>
    );
}

function SamplePrevArrow(props) {
    const { id, className, onClick } = props
    return (
        <i id="prev" className={"material-icons"} onClick={onClick}>arrow_back</i>
    );
}

class Slideshow extends Component {
    render() {
        var settings = {
            dots: true,
            infinite: true,
            speed: 500,
            slidesToShow: 1,
            slidesToScroll: 1,
            nextArrow: <SampleNextArrow />,
            prevArrow: <SamplePrevArrow />
        };

        let slides = []
        slides.push({
            title: "Escolha uma academia",
            description: "Aquela mais perto de voc, ou a que tem um aparelho que voc gosta. Quer fazer alguma atividade especifica? Utilize nosso filtro!"
        })
        slides.push({
            title: "Selecione um plano",
            description: "Veja os preos de da musculao, das danas, lutas e etc..."
        })
        slides.push({
            title: "Acompanhe suas Atividades",
            description: "Defina seu treino com o Instrutor e acompanhe na tela, receba dicas e alertas."
        })
        slides.push({
            title: "Monitore o seu progresso!",
            description: "Acompanhe o seu desenvolvimento, compartilhe com seus amigos e mantenha-se focado."
        })

        var index = 0;

        return (
            < Slider {...settings }>
                {slides.map(slide => <div><SliderItem id={++index + "."} title={slide.title} description={slide.description} /></div>)}
            </Slider >
        );
    }
}

export default Slideshow;


