import React, { Component } from 'react';
import logo from '../assets/images/logo.svg';
import Links from '../views/header/links';

class Header extends Component {
  render() {
    return (
      <header>
        <div className="logo">
          <img src={logo} alt="Logo" />
        </div>
        <Links />
      </header>
    );
  }
}

export default Header;


