import React, { Component } from 'react';
import logo from '../../assets/images/logobranca.svg';
import Links from '../../views/footer/links';

class Left extends Component {
  render() {
    return (
        <div className='left'>
            <img src={logo} className='logo' alt="logo"/>
            <p>2017, Ufit. Todos os direitos reservados.<br/>Feito em Salvador, Bahia</p>
            <Links/>
        </div>
    );
  }
}

export default Left;


