import React, { Component } from 'react';

class Intro extends Component {
  render() {
    return (
      <section id="intro" className='intro'>
        <div className='description'>
          <h1>Academias Inteligentes perto de voc!</h1>
          <p>Nosso objetivo  tornar as academias mais tecnolgicas e inovadoras, proporcionando dados reais sobre sua rotina para mant-lo focado na sua conquista pessoal.</p>
        </div>
      </section>
    );
  }
}

export default Intro;



