import React, { Component } from 'react';

import Intro from './intro'
import Stakeholders from './stakeholders'
import Slider from './slider'

class Home extends Component {
  render() {
    return (
      <div>
        <Intro />
        <Stakeholders />
        <Slider />
      </div>
    );
  }
}

export default Home


